export default {
	 name:'Toast',
	 props: {
		  className:{
			type: String,
			default:''
		  }
		},
		data() {
			return {
			   visible:false,
			   message:'',
			   position:'placemiddle',
			   duration:2000,
			   icon:''
			}
		},
		onLoad(e) {
			
		},
		created() {
	    
	    },
		methods: {
	        hide() { //显示头部
			  this.visible=false;
	        },
			show(options){
			console.log(options)
		     this.message = typeof options === 'string' ? options : options.message;
			 this.position=options.position || "placemiddle";
		     this.duration=options.duration || this.duration;
			 this.icon=options.icon;
			 this.visible=true;
			 if(options.duration==0) return;
			  setTimeout(()=>{
					this.visible=false;
				}, this.duration);
			}
		},
		 computed: {
			
        }
	}
